
.bodyIn{
    background: #89609E;
    background-size: cover;
    position: relative;
    height: 100vh;
    margin-bottom: 10%;
}
.form-horizontal .form-group{
    margin-right: 0px;
}
.logo{
    display: inline-block;
    background-size: contain;
    vertical-align: middle;
    height: 70px;
    width: 206px;
    margin: 1.5em 0;
}

.inputIn{
    border: 0px none;
    width: 350px;
    padding: 1.1em;
    font-size: 1.1em;
    border-radius: 3px;
    -webkit-box-shadow: none;
            box-shadow: none;
    line-height: 1.2em;
    margin: .3em 0;
}

.btnIn{
    -webkit-transition: all 0.15s ease-out 0s;
    -o-transition: all 0.15s ease-out 0s;
    transition: all 0.15s ease-out 0s;
    width: 350px;
    padding: 1.1em;
    font-size: 1.1em;
    background: transparent;
    color:#fff;
    border: 1px solid #fff;
    border-radius: 3px;
    -webkit-box-shadow: none;
            box-shadow: none;
    line-height: 1.2em;
    margin: .3em 0;
    width:100%;
}
.btnIn:hover{
    color: #89609E !important;
    background-color: rgba(255, 255, 255, 0.37);
}

.navlinkIn, .navlinkIn:hover{
    text-decoration: none;
    color: #bea7ca;
    font-weight: 600;  
}




